module.exports = "/* HEADER */\n\nheader {background-color: var(--n-dark);}\n\nheader h1 {\n    color: var(--text-inv);\n    background-color: var(--n-dark);\n    padding: 1.1rem 0 0 45px;\n    margin-left: 5%;\n  }\n\nh1 img {max-width: 175px;}\n\nheader > button {\n    position: absolute;\n    top: 0;\n    left: 5%;\n    background: initial;\n    padding: 1rem;\n    margin-left: -1rem;\n    transition: -webkit-transform 0.7s;\n    transition: transform 0.7s;\n    transition: transform 0.7s, -webkit-transform 0.7s;\n  }\n\nheader > button img {width: 25px; height: 25px;}\n\n/* If Burger Menu Open */\n\n.mobileOpen {\n  height: 100%;\n  left: 0;\n  transition: left 0.6s;\n}\n\n/* Else if Burger Menu Close */\n\n.mobileClose {transition: left 0.6s;}\n\n/* Rotate Burger Button */\n\n.burgerVertical {-webkit-transform: rotateZ(90deg);transform: rotateZ(90deg);}\n\nc3m-menu {\n  position: absolute;\n  left: -1000px;\n  width: 80%;\n  max-width: 350px;\n  height: auto;\n  color: var(--text-inv);\n  background-color: var(--n-dark);\n  transition: left 0.6s;\n  z-index: 999;\n}\n\n@media screen and (min-width: 1000px) {\n\n  header {height: 100%;}\n    header .logo {\n      display: block;\n      text-align: center;\n      padding: 5vh 0 9vh;\n    }\n    header h1 {padding: 0; margin: 0;}\n    header > button {display: none;}\n    header > p {padding: 0;}\n\n    header .mobileClose, .mobileOpen {\n      left: 0;\n      height: auto;\n      background-color: var(--n-dark);\n    }\n\n  c3m-menu {\n    display: block;\n    position: static;\n    overflow: hidden;\n    width: auto;\n    height: auto;\n    top: 0;\n  }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"