module.exports = "/* Dropdown */\nc3m-dropdown{\n  position:relative;\n}\n/* Dropdown Content */\nc3m-dropdown ul {\n    display: none;\n    width: 20%;\n    transition: all .25s ease;\n  }\n/* Dropdown Content List Item */\nc3m-dropdown li{\n      background-color: #f3ba65\n    }\n/* Link Of Dropdown Content List Item */\nc3m-dropdown a {\n        color: white;\n        display: block;\n        padding: 0.5rem 1rem;\n        text-align: left;\n        text-decoration: none;\n        transition: all .25s ease;\n      }\n/* Dropdown Button */\nc3m-dropdown > button{\n          padding: 0.5rem 1rem;\n          background-color: #44abbc;\n          color: white;\n          cursor : pointer;\n        }\nc3m-dropdown button:hover{\n            background-color: #58c5d8;\n          }\nc3m-dropdown > button span{\n            display: inline-block;\n          }\nc3m-dropdown > button svg{\n            display: inline-block;\n            height: 24px;\n            width: 24px;\n            fill: white;\n            margin-left: 0.4rem;\n            transition: -webkit-transform 0.5s;\n            transition: transform 0.5s;\n            transition: transform 0.5s, -webkit-transform 0.5s;\n            vertical-align: top;\n            margin-top: -0.1rem;\n          }\n/* Display Dropdown Content List if Button Click */\nc3m-dropdown ul.open {\n    display: block;\n    position: absolute;\n    top: calc(100% + 1.3rem);\n    left: 0;\n    width: 14rem;\n  }\nc3m-dropdown > button.open svg{\n      -webkit-transform: rotate(180deg);\n              transform: rotate(180deg);\n    }\nc3m-dropdown ul li {\n        width: 100%;\n    }\nc3m-dropdown ul a:hover {\n        background: #e0ac5d;\n    }\n\n/*# sourceMappingURL=data:application/json;base64, */"