module.exports = "/* Global p */\nc3m-input-floating > p {\n  position: relative;\n  overflow: hidden;\n  width: 40%;\n  font-size: 1rem;\n  min-width: 220px;\n}\n/* Input */\nc3m-input-floating input {\n  width: 100%;\n  border: 2px solid gray;\n  background: none;\n  position: relative;\n  top: 0;\n  left: 0;\n  z-index: 1;\n  padding: 8px 12px;\n  outline: 0;\n  font-family: quicksandregular;\n  box-sizing: border-box;\n}\n/* If Valid */\nc3m-input-floating input:valid {\n    background: white;\n  }\n/* If Focus */\nc3m-input-floating input:focus {\n    border-color: #f06d06;\n  }\n/* Label If Input Focused */\nc3m-input-floating input:focus + label {\n    background: #f06d06;\n    color: white;\n    font-size: 70%;\n    padding: 1px 6px;\n    z-index: 2;\n    text-transform: uppercase;\n  }\n/* Label */\nc3m-input-floating label {\n  transition:\n    background 0.2s,\n    color 0.2s,\n    top 0.2s,\n    bottom 0.2s,\n    right 0.2s,\n    left 0.2s;\n  position: absolute;\n  color: #999;\n  padding: 7px 6px;\n}\n/* Input with Floating Bottom */\nc3m-input-floating p.bottom > input {\n  padding: 12px 0 12px 0;\n}\n/* Label with Floating Bottom */\nc3m-input-floating p.bottom > label {\n  top: 0;\n  bottom: 0;\n  left: 0;\n  width: 100%;\n}\n/* Input Focused with Floating Bottom */\nc3m-input-floating p.bottom > input:focus {\n  padding: 4px 6px 20px 6px;\n}\n/* Label with Input Focused FLoating Bottom */\nc3m-input-floating p.bottom > input:focus + label{\n  top: 100%;\n  margin-top: -16px;\n}\n/* **** RIGHT ***** */\nc3m-input-floating p.right label {\n    top: 2px;\n    right: 100%;\n    width: 100%;\n    margin-right: -100%;\n    bottom: 2px;\n  }\nc3m-input-floating p.right input:focus + label {\n    right: 0;\n    margin-right: 0;\n    width: 40%;\n    padding-top: 5px;\n  }\n\n/*# sourceMappingURL=data:application/json;base64, */"