module.exports = "/* INPUT CSS */\n\nc3m-slider .slider-content {position: relative;}\n\nc3m-slider.bubble .slider-content {margin-bottom: 2em;}\n\n/* DEFAULT INPUT */\n\nc3m-slider input[type=range] {\n  display: inline-block;\n  width: 50%;\n  background-color: rgba(0,0,0,0);\n  padding: 0;\n  border: none;\n  -webkit-appearance: none;\n  -moz-appearance: none;\n  appearance: none;\n}\n\n/* DEFAULT INPUT FOCUS */\n\nc3m-slider input[type=range]:focus {\n      outline: none;\n  }\n\n/* TRACK */\n\nc3m-slider input[type=range]::-webkit-slider-runnable-track {\n      width: 100%;\n      height: 8px;\n      background: var(--main-light);\n      border-radius: 8px;\n      box-shadow: none;\n      cursor: pointer;\n  }\n\n/* BALL */\n\nc3m-slider input[type=range]::-webkit-slider-thumb {\n      height: 26px;\n      width: 26px;\n      background: var(--sec-light);\n      margin-top: -9px;\n      border-radius: 26px;\n      -webkit-appearance: none;\n      box-shadow: 1px 1px 2px rgba(0,0,0,.5);\n      cursor: pointer;\n  }\n\n/* FOCUS TRACK */\n\nc3m-slider input[type=range]:focus::-webkit-slider-runnable-track {\n      background: #45ABBC;\n  }\n\n/* MOZ TRACK */\n\nc3m-slider input[type=range]::-moz-range-track {\n      width: 100%;\n      height: 8px;\n      background: var(--main-light);\n      border-radius: 8px;\n      box-shadow: none;\n      cursor: pointer;\n  }\n\n/* MOZ BALL */\n\nc3m-slider input[type=range]::-moz-range-thumb {\n      height: 26px;\n      width: 26px;\n      background: var(--sec-light);\n      border: none;\n      border-radius: 26px;\n      box-shadow: 1px 1px 2px rgba(0,0,0,.5);\n      cursor: pointer;\n  }\n\n/* MS TRACK */\n\ninput[type=range]::-ms-track {\n      width: 100%;\n      height: 8px;\n      cursor: pointer;\n      background: transparent;\n      border-color: transparent;\n      color: transparent;\n  }\n\nc3m-slider input[type=range]::-ms-fill-lower {\n      background: var(--main-light);\n      border: 0.2px solid var(--n-medium);\n      border-radius: 2.6px;\n      box-shadow: none;\n  }\n\nc3m-slider input[type=range]::-ms-fill-upper {\n      background: var(--main-light);\n      border: 0.2px solid var(--n-medium);\n      border-radius: 2.6px;\n      box-shadow: none;\n  }\n\nc3m-slider input[type=range]::-ms-thumb {\n    height: 26px;\n    width: 26px;\n    border-radius: 12px;\n    background: var(--sec-light);\n    cursor: pointer;\n    height: 8.4px;\n}\n\nc3m-slider input[type=range]:focus::-ms-fill-lower {\n    background: var(--main-light);\n}\n\nc3m-slider input[type=range]:focus::-ms-fill-upper {\n    background: var(--main-light);\n}\n\n/* Output */\n\nc3m-slider output {\n  position: relative;\n  top: 0.3em;\n  font: 1.2rem var(--stack-b);\n  text-transform: uppercase;\n  color:  var(--main-color);\n  margin-left: 1rem;\n}\n\nc3m-slider.bubble output {\n      position: absolute;\n      display: inline-block;\n      top: auto;\n      bottom: -2.4rem;\n      left: 0;\n      width: 40px;\n      height: 26.2px;\n      text-align: center;\n      color: var(--text-inv);\n      background-color: var(--n-dark);\n      padding-top: 0.3rem;\n      margin-left: 0;\n      border-radius: 10px;\n      -webkit-transform: translate(-50%);\n              transform: translate(-50%);\n    }\n\nc3m-slider.bubble output:after {\n            content: \"\";\n            position: absolute;\n            width: 0;\n            height: 0;\n            border-bottom: 10px solid var(--n-dark);\n            border-left: 5px solid transparent;\n            border-right: 5px solid transparent;\n            bottom: 100%;\n            left: 50%;\n            margin-left: -5px;\n            margin-top: -1px;\n        }\n\n/*# sourceMappingURL=data:application/json;base64, */"